//  color
$black:    #000 !default;
$green:   #28a745 !default;
$green:   #10c9c3;

$success:   $green !default;

// border
$border-width:               0;
$border-radius:               .25rem;
$border-radius-lg:            .3rem;
$border-radius-sm:            .2rem;

$border-radius:               .25rem !default;
$border-radius-lg:            .1rem;
$border-radius-sm:            .2rem !default;


//card-border
$card-bg:transparent;
$card-border-radius:  $border-radius;
$card-border-width:   $border-width;
$card-border-color:   rgba($black, .125) !default;


//input-btn-padding
$input-btn-padding-y-lg:      .8rem !default;
$input-btn-padding-x-lg:      2rem !default;

$padding-page: 80px;


// font-size
$font-size-base:              1rem !default; 
$font-size-lg:                ($font-size-base * .9);
$font-size-sm:                ($font-size-base * .875) !default;

// fonts-weights
 $weights: (
    thin: 100,
    extra-light: 200,
    ultra-light: 200,
    light: 300,
    normal: 400,
    book: 400,
    regular: 400,
    medium: 500,
    semi-bold: 600,
    demi-bold: 600,
    bold: 700,
    extra-bold: 800,
    ultra-bold: 900,
    heavy: 900,
    black: 900,
    ultra: 900,
    ultra-black: 900,
    extra-ultra: 900
  );

//link-padding
$nav-link-padding-y: 0;



//form
$form-group-margin:              1rem 0;

.form-group {
  margin: $form-group-margin;
}


//carousel_weight and height
$carousel-indicator-width:          10px;
$carousel-indicator-height:         10px;



